.history_manage {
  padding: 0 1.5rem 1.5rem;
  .history_tab  {
    border-radius: .625rem;
    padding: .9375rem 1.25rem;
    background: #FFFFFF;
    // display: flex;
    position: relative;
    margin-bottom: .625rem;
    .ant-tabs {
      // width: calc(100% - 7rem);
    }
    .header_usage {
      position: absolute;
      top: 1.625rem;
      right: 1.25rem;
      display: flex;
      &>.ant-btn +.ant-btn {
        margin-left: .9375rem;
      }
    }
  }
}

.history_list {
  .hl_content {
    padding: 1.25rem;
    border-radius: .625rem;
    background: #FFFFFF;
    .hl_header {
      display: flex;
      justify-content: space-between;
      // padding: .75rem 0;
      margin-bottom: .625rem;
      flex-wrap: wrap;
      .cop_title {
        font-size: 1.5rem;
        font-weight: 700;
        color: #333;
        margin-top: .75rem;
      }
      .hl_header_lt {
        
      }
      .hl_header_gt {
        display: flex;
  
        &>* {
          margin-left: .9375rem;
        }
      }
    }
    .operate_box {
      display: flex;
      align-items: center;
      justify-content: center;
      .devide_line {
        width: .0625rem;
        min-width: .0625rem;
        height: .75rem;
        background-color: #D8D8D8;
        margin: 0 .625rem;
      }
    }
  }
}

.sensitive_word {
  .sw_header {
    padding: 0 1.5rem 1.5rem;
    background: #FFFFFF;
    display: flex;
    justify-content: space-between;
    // padding: .75rem 0;
    // margin-bottom: .625rem;
    flex-wrap: wrap;
    .cop_title {
      font-size: 1.5rem;
      font-weight: 700;
      color: #333;
      margin-top: .75rem;
    }
    .sw_header_lt {
      
    }
    .sw_header_gt {
      display: flex;

      &>* {
        margin-left: .9375rem;
      }
    }
  }
  .sw_content {
    padding: 1.25rem;
    border-radius: .625rem;
    background: #FFFFFF;
    margin: 1.5rem;
    .operate_box {
      display: flex;
      align-items: center;
      justify-content: center;
      .devide_line {
        width: .0625rem;
        min-width: .0625rem;
        height: .75rem;
        background-color: #D8D8D8;
        margin: 0 .625rem;
      }
    }
  }
}

.sw_form {
  &>div {
    display: block;
  }
  .ul_form_title {
    font-size: .875rem;
    margin: .75rem 0 .5rem;
  }
  .ul_form_info {
    font-size: .75rem;
    color: red;
  }
}